import {Component, OnInit} from '@angular/core';
import {Order} from "@models/order";
import {OrderService} from "@services/order.service";
import {AuthService} from "@services/auth.service";

@Component({
  selector: 'app-user-orders',
  templateUrl: './user-orders.component.html',
  styleUrls: ['./user-orders.component.scss']
})

export class UserOrdersComponent implements OnInit {

  orders: Order[] = [];
  ordersById = [];
  ordersPrice = [];

  constructor(private orderService: OrderService, private authService: AuthService) {
  }

  ngOnInit() {
    this.orderService.getOrdersOfUser(this.authService.getDecodedToken().sub).subscribe(orders => {
      this.orders = orders;
   //   this.createOrders();
   //   console.log(this.orders);
    });

  }

  createOrders() {
    if (this.orders.length > 0) {
      for (let i = 0; i < this.orders.length; ++i) {
        let sum = 0;
        let countI = 0;
        for (let j = 0; j < this.orders.length; ++j) {
          if (this.orders[i].id === this.orders[j].id) {
            sum += this.orders[j].price;
            countI++;
          }
        }
        this.ordersPrice[i] = ({orderDate: this.orders[i].orderDate, price: sum});

        i += countI;
      }

      console.log(this.ordersPrice);

      for (let i = 0; i < this.orders.length; ++i) {
        this.ordersById[i] = [];
        let countI = 0;
        for (let j = 0; j < this.orders.length; ++j) {
          if (this.orders[i].id === this.orders[j].id) {
            this.ordersById[i][j] = ({name: this.orders[j].name, price: this.orders[j].price});
            countI++;
          }
        }
        i += countI;
      }
    }
    console.log(this.ordersById);
  }

}
