import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ChatMessageGlobal} from "@models/chat.models";
import {StompClientService} from "@services/stomp-client.service";
import {AuthService} from "@services/auth.service";
import {Utils} from "@helpers/utils.service";

@Component({
  selector: 'app-global-chat',
  templateUrl: './global-chat.component.html',
  styleUrls: ['./global-chat.component.scss']
})
export class GlobalChat implements OnInit {

  username: string;
  userId: number;
  messages: ChatMessageGlobal[] = [];
  value = null;
  role: string;
  userEmail: string = null;
  notConfirmedMessages: ChatMessageGlobal[] = [];
  gravatar = Utils;

  @ViewChild('globalChat') private scrollContainer: ElementRef;

  constructor(private stompClientService: StompClientService,
              private authService: AuthService) {
  }

  ngOnInit() {
    this.userEmail = this.authService.getDecodedToken().email;

    this.stompClientService.messageInGlobalChat.subscribe(
      data => {
        if (data) {
          if (data && data.userId == this.userId) {
            this.checkIfSent(data);
          } else {
            this.messages.push(data);
          }
          Utils.scrollToBottom(this.scrollContainer);
        }
      }
    );
    let token = this.authService.getDecodedToken();
    this.username = token.username;
    this.userId = token.sub;
    this.role = token.authority;
    Utils.scrollToBottom(this.scrollContainer);
  }

  sendMessage(message: string) {
    if (!message.trim()) {
      return
    }
    let messagePayload = {
      username: this.username,
      userId: this.userId,
      role: this.role,
      message: message,
      time: new Date(),
      destinationId: this.userId,
      email: this.userEmail
    };

    this.stompClientService.sendMessageToGlobalChat(messagePayload);
    this.notConfirmedMessages.push(messagePayload);
    this.messages.push(messagePayload);
    this.value = null;
    Utils.scrollToBottom(this.scrollContainer);
  }

  onKeyDown(event, msg: string) {
    if (event.key == 'Enter') {
      this.sendMessage(msg);
      event.preventDefault();
    }

  }

  checkIfSent(message: ChatMessageGlobal) {
    if (this.notConfirmedMessages.indexOf(message)) {
      this.notConfirmedMessages.splice(this.notConfirmedMessages.indexOf(message), 1);
    }
  }
}
